<template>
  <div class="project-pre-views"
       v-show="isShow"
       @click.stop="_click">
    <div class="close"
         @click.stop="_close">X</div>
    <div class="pre-container">
      <img v-for="(item, index) in preDatas"
           :key="index+''"
           :src="item"
           alt="">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    type: String
  },
  data () {
    return {
      isShow: this.value,
    }
  },
  computed: {
    preDatas: function () {
      return this._selDatas()
    }
  },
  methods: {
    _click () {
      // this.isShow = false
      // this.$emit('input', false)
    },
    _close () {
      this.isShow = false
      this.$emit('input', false)
    },
    _displayerDOM (show) {
      const body = document.querySelector('body')
      if (show) {
        body.append(this.$el)
      } else {
        body.removeChild(this.$el)
      }
    },
    _selDatas () {
      let datas = []
      switch (this.type) {
        case "rrk": {
          datas = [
            require("@/assets/rrk460x01.webp"),
            require("@/assets/rrk460x02.webp"),
            require("@/assets/rrk460x03.webp"),
            require("@/assets/rrk460x04.webp")]
          break
        }
        case "jzsz": {
          datas = [
            require("@/assets/jzsz460x01.webp"),
            require("@/assets/jzsz460x02.webp"),
            require("@/assets/jzsz460x03.webp"),
            require("@/assets/jzsz460x04.webp")]
          break
        }
        case "ddqf": {
          datas = [
            require("@/assets/ddqk460x01.webp"),
            require("@/assets/ddqk460x02.webp"),
            require("@/assets/ddqk460x03.webp")]
          break
        }
        case "ddzf": {
          datas = [
            require("@/assets/ddzf460x01.webp"),
            require("@/assets/ddzf460x02.webp"),
            require("@/assets/ddzf460x03.webp")]
          break
        }
        case "jk": {
          datas = [
            require("@/assets/jk01.jpg"),
            require("@/assets/jk02.jpg"),
            require("@/assets/jk03.jpg"),
            require("@/assets/jk04.jpg")]
          break
        }
        case "ccbdx": {
          datas = [
            require("@/assets/ccbdx460x01.webp"),
            require("@/assets/ccbdx460x02.webp"),
            require("@/assets/ccbdx460x03.webp"),
            require("@/assets/ccbdx460x04.webp")]
          break
        }
      }
      return datas
    }
  },
  watch: {
    value: function (newVlu) {
      this.isShow = newVlu
      this._displayerDOM(newVlu)
    }
  },
  created () {


  },
  mounted () {
    if (this.value) {
      this._displayerDOM(true)
    }
  }
}
</script>

